// src/scripts/pages/home.js

import { initMap, addMarker } from '../map';
import { fetchStories }         from '../presenters/homePresenter';
import {
  saveStoriesToIDB,
  getStoriesFromIDB,
  deleteStoryFromIDB,
}                                from '../db.js';

export async function renderHome(container) {
  const token = localStorage.getItem('token');
  if (!token) {
    location.hash = '#/login';
    return;
  }

  container.innerHTML = `
    <section class="page home-page">
      <a id="mainContent" tabindex="-1"></a>
      <h2>Semua Cerita</h2>
      <button id="logoutBtn">Logout</button>
      <div id="storiesContainer">Memuat cerita…</div>
      <div style="width:100%;">
        <div id="map" class="map-fullwidth"></div>
      </div>
    </section>
  `;

  // accessibility & logout
  document.querySelector('.skip-link')?.addEventListener('click', () => {
    document.getElementById('mainContent')?.focus();
  });
  document.getElementById('logoutBtn').addEventListener('click', () => {
    localStorage.removeItem('token');
    location.hash = '#/login';
  });

  // 1) load from network or IndexedDB
  let stories = [];
  try {
    stories = await fetchStories(token);
    await saveStoriesToIDB(stories);
  } catch (err) {
    console.warn('Fetch gagal, fallback ke IDB:', err);
    stories = await getStoriesFromIDB();
  }

  // 2) render & wire up delete buttons
  renderStories(stories);
  attachDeleteHandlers();

  // 3) init map
  const map = initMap();
  stories.forEach(s => {
    if (s.lat && s.lon) addMarker(map, s.lat, s.lon, `${s.name}: ${s.description}`);
  });
}

function renderStories(stories) {
  const html = stories.map(s => `
    <div class="story" data-id="${s.id}">
      <a href="#/story/${s.id}" class="story-link">
        <img src="${s.photoUrl}" alt="Foto oleh ${s.name}" width="200"
             style="view-transition-name: story-${s.id};">
      </a>
      <h4>${s.name}</h4>
      <p>${s.description}</p>
      <small>${new Date(s.createdAt).toLocaleString()}</small>
      <br>
      <button class="delete-button" data-id="${s.id}">Hapus</button>
    </div>
  `).join('');
  document.getElementById('storiesContainer').innerHTML = html || 'Tidak ada cerita.';
}

function attachDeleteHandlers() {
  document.querySelectorAll('.delete-button').forEach(btn => {
    btn.addEventListener('click', async () => {
      if (!confirm('Yakin ingin menghapus cerita ini?')) return;
      const id = btn.dataset.id;
      await deleteStoryFromIDB(id);
      const remaining = await getStoriesFromIDB();
      renderStories(remaining);
      attachDeleteHandlers();
    });
  });
}
